main {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.mapper {
  position: relative;
  width: 100vw;
  height: 100%;
  display: flex;
}

.mapper .mapper-left {
  position: relative;
  height: 100%;
  flex: 1 1 auto;
}

.mapper .mapper-left_wrapper {
  position: fixed;
  right: calc(100% - (100vw - 572px));
  bottom: 0;
  left: 0;
  overflow: hidden;
  height: 100%;
}

.mapper .mapper-right {
  position: relative;
  width: 572px;
  height: 100%;
  max-width: 572px;
  border-left: 1px solid #e6e6e6;
  background: rgba(252, 252, 253, 0.9);
  flex: 1 1 572px;
}

.mapper .mapper-right_wrapper {
  position: relative;
  min-height: 100vh;
  padding: 60px 0 0;
}

@media screen and (min-width: 771px) {
  .mapper .mapper-right_wrapper {
    margin-right: 15px;
  }
}
